.order-table {
  width: 100%;
  max-width: 670px;
  min-height: 194px;

  border-spacing: 0 30px;

  margin: 0 20px 0 auto;

  & tr {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: 600;

    & td:last-of-type {
      display: flex;
      align-items: center;
      justify-content: center;

      width: 100%;
      max-width: 190px;
      min-height: 40px;

      text-align: center;

      background-color: $color-default-white;
    }
  }

  & tbody {
    font-size: 20px;
    line-height: 15px;
    letter-spacing: 0.05em;

    & tr {
      margin: 0 0 30px;

      &:last-of-type {
        margin: 0 0 10px;
      }
    }
  }

  & tfoot {
    font-weight: 600;
    font-size: 20px;
    line-height: 22px;
    letter-spacing: 0.05em;
    text-transform: uppercase;

    & td:last-of-type {
      font-weight: 700;
      font-size: 24px;
      line-height: 26px;
    }

    & span {
      position: relative;
      left: 3px;
      font-weight: 400;
      font-size: 24px;
      font-family: system-ui;

      margin-left: 5px;
    }
  }

}
