.catalog-sort {
  font-size: 16px;
  line-height: 20px;

  display: flex;
  justify-content: flex-start;

  width: 100%;
  padding-top: 3px;

  letter-spacing: 0.05em;

  grid-area: sorter;
}

.catalog-sort__title {
  font: inherit;

  margin: 0 20px 0 0;

  color: $color-default-black;
}

.catalog-sort__type {
  display: flex;
  flex-wrap: wrap;

  margin: 0;
  padding: 0;

  list-style: none;

  color: $color-matterhorn-3;
}

.catalog-sort__type-button {
  position: relative;

  padding: 0;

  cursor: pointer;
  transition: color $trans--300, outline-color $trans--300;
  color: $color-matterhorn-3;
  letter-spacing: 0.05em;

  border: none;
  background-color: $color-transparent;

  &:not(:last-child) {
    margin-right: 30px;
  }

  &:hover {
    color: $color-free-speech-red;
  }

  &:focus-visible {
    outline: none;

    &::before {
      position: absolute;
      content: "";
      bottom: -3px;
      width: 100%;
      height: 2px;
      background-color: $color-free-speech-red;
    }
  }
}

.catalog-sort__type-button--active {
  pointer-events: none;

  color: $color-default-black;

  &::before {
    position: absolute;
    content: "";
    bottom: -3px;
    width: 100%;
    height: 2px;
    background-color: $color-free-speech-red;
  }
}

.catalog-sort__order {
  display: flex;
  align-items: center;

  margin-right: 9px;
  margin-left: auto;
}

.catalog-sort__order-button {
  width: 0;
  height: 0;
  padding: 0;

  cursor: pointer;
  transition: border-top-color $trans--300, outline-color $trans--300;

  border-width: 12px 7px 0;
  border-color: transparent;
  border-top: 12px solid $color-matterhorn-3;
  background-color: transparent;

  @include expand-interactive-area(30px, 30px);

  &--up {
    transform: rotate(180deg);
  }

  &--down {
    margin-left: 23px;

    &::before {
      transform: translate(-50%, -75%);
    }
  }

  &--active {
    pointer-events: none;

    border-top: 12px solid $color-free-speech-red;
  }

  &:hover,
  &:focus {
    outline: none;
    border-top: 12px solid $color-free-speech-red;
  }
}
