.custom-input {
  label {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  span {
    font-family: $font-family--main;
    font-size: 20px;
    font-weight: 400;
    line-height: 25px;
    letter-spacing: 0.05em;
    color: $color-default-black;
    margin-right: auto;
    margin-bottom: 33px;
  }

  input {
    display: flex;
    align-items: center;
    min-height: 40px;
    font-size: 16px;
    font-weight: 600;
    line-height: 22px;
    letter-spacing: 0.05em;
    color: $color-default-black;
    box-sizing: border-box;
    border: 1px solid $color-default-black;
    outline: none;
    background-color: $color-default-white;
    text-align: right;
    padding: 5px 22px;
    border-radius: 2px;
    width: 390px;
    margin-left: auto;

    &::placeholder {
      color: $color-grey;
      font-size: 16px;
    }
  }

  sub {
    color: $color-free-speech-red;
    top: -10px;
  }

  p {
    width: 100%;
    position: relative;
    opacity: 0;
    margin: 0;
    font-family: $font-family--main;
    font-size: 10px;
    font-weight: 400;
    line-height: 15px;
    letter-spacing: 0.02em;
    color: $color-flamingo;
    transition: opacity $trans--300;
    text-align: right;
  }

  &.is-invalid {
    p {
      opacity: 1;
    }
  }
}
