.edit-item-image {
  width: 260px;

  &__image-wrap {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 17px;
    height: 370px;
    box-sizing: border-box;
    border: 1px solid $color-default-black;
    background-color: $color-default-white;
    box-shadow: inset 0 0 0 4px $color-snow;
  }

  &__image {
    filter: drop-shadow(0 2px 6px rgba(0, 0, 0, 0.2));
    object-fit: contain;
  }

  &__btn-wrap {
    display: grid;
    grid-template-columns: 100px 100px;
    column-gap: 47px;
    row-gap: 15px;
    padding: 0 7px;
  }

  &__btn {
    padding: 5px;
    min-width: 100px;
    min-height: 40px;
    font-size: 11px;
    letter-spacing: 0.02em;
  }
}
