.edit-item {
  &__title {
    margin: 0 0 15px;
    font-family: $font-family--main;
    font-size: 22px;
    font-weight: 700;
    line-height: 30px;
    color: $color-black-1;
  }

  &__form {
    display: grid;
    grid-template-columns: 260px 1fr;
    column-gap: 46px;
    padding-top: 79px;
  }

  &__form-image {
    margin-bottom: 48px;
  }

  &__form-input--price {
    position: relative;

    input {
      font-size: 20px;
      line-height: 25px;
      padding-right: 30px;
    }

    &::before {
      content: "";
      position: absolute;
      bottom: 24px;
      right: 22px;
      width: 10px;
      height: 15px;
      background-image: url("../img/sprite/icon-rouble.svg");
      background-repeat: no-repeat;
    }
  }

  &__form-textarea {
    margin-top: 13px;
    margin-bottom: 83px;
  }

  &__form-buttons-wrap {
    display: grid;
    grid-template-columns: 200px 200px;
    column-gap: 84px;
    row-gap: 15px;
    margin: 0 auto;
    grid-column: span 2;
  }

  &__form-button {
    padding: 5px;
    width: 100%;
    min-height: 40px;
    font-family: $font-family--main;
    font-size: 11px;
    line-height: 15px;
    letter-spacing: 0.02em;
  }
}

.edit-item__form-radio + .edit-item__form-radio {
  margin-top: 83px;
}

.is-placeholder.edit-item__form-input--price {
  &::before {
    background-image: url("../img/sprite/icon-rouble-placeholder.svg");
  }
}
