.header {
  display: flex;

  min-height: 110px;
  padding-top: 22px;
  padding-bottom: 12px;

  color: $color-default-white;
  background-color: $color-seal-brown;

  &__wrapper {
    display: flex;
    align-content: center;
    align-items: center;

    width: 100%;
    padding: 0 7px 0 0;

    background: inherit;
  }

  &__logo {
    margin-right: 110px;
  }

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;

    min-width: 56px;
    margin: 0 0 0 auto;
  }

  &__user-name,
  &__link-text {
    display: none;
  }

  &__link {
    background-color: transparent;

    padding: 16px 12px 11px;

    display: block;
    position: relative;

    &:hover .header__link-text,
    &:focus .header__link-text,
    &:active .header__link-text {
      color: $color-default-black;
      font-size: 10px;
      line-height: 15px;
      letter-spacing: 0.05em;
      text-decoration: underline;
      text-underline-offset: 3px;
      background-color: $color-default-white;

      display: flex;
      align-items: center;
      justify-content: center;

      position: absolute;
      width: 60px;
      min-height: 20px;
      top: 45px;
      right: -6px;
      border-radius: 3px;
    }

    &:hover .header__link-icon,
    &:focus .header__link-icon,
    &:active .header__link-icon {
      opacity: 0.7;
      transition: opacity $trans--300;
    }
  }

  &__link-icon {
    stroke: $color-default-white;
    fill: transparent;
  }

  &__cart-link {
    position: relative;

    display: block;

    width: 14px;
    height: 14px;
    padding: 14px;

    color: $color-default-white;

    @include transition-color;
    @include expand-interactive-area(40px, 40px);

    &:hover,
    &:focus {
      color: $color-free-speech-red;
    }
  }

  &__cart-icon {
    position: absolute;
    top: 50%;
    left: 50%;

    width: 14px;
    height: 14px;

    transform: translate(-50%, -50%);
  }

  &__cart-count {
    display: none;
  }
}

.header--logged,
.header--logged-empty,
.header--admin {
  & .header__container {
    min-width: 103px;
  }

  & .header__user-name {
    display: block;
  }

  & .header__link {
    display: flex;
    align-items: center;
    justify-content: center;

    background-color: $color-gainsboro;

    width: 20px;
    height: 20px;
    border-radius: 50%;

    padding: 0;

    position: relative;
    cursor: pointer;

    & .header__link-text,
    &:hover .header__link-text,
    &:focus .header__link-text,
    &:active .header__link-text {
      display: none;
    }

    &:hover,
    &:focus,
    &:active {
      opacity: 0.7;
      transition: opacity $trans--300;
    }
  }

  & .header__link-icon {
    stroke: $color-default-black;
    fill: transparent;
  }
}

.header--logged {
  & .header__cart-link {
    position: relative;
  }

  & .header__link {
    background-color: $color-gainsboro;

    padding: 0;
  }

  & .header__cart-count {
    font-family: $font-family--secondary;
    font-size: 10px;
    line-height: 12px;

    display: block;
    position: absolute;
    top: 1px;
    right: -7px;

    width: 12px;
    height: 12px;

    text-align: center;

    color: $color-default-white;
    border-radius: 50%;
    background-color: $color-free-speech-red;
  }
}

.header--logged-empty {
  & .header__link {
    background-color: $color-gainsboro;

    padding: 0;
  }

  & .header__cart-count {
    display: none;
  }
}

.header--admin {
  & .header__link {
    background-color: $color-flamingo;

    padding: 0;
  }

  & .header__cart-count {
    display: none;
  }
}
