.orders {
  &__title {
    margin: 5px 0 15px;
  }

  &__breadcrumps {
    margin-bottom: 66px;
  }

  &__list {
    padding: 0;
    margin: 37px 0 81px;
    list-style: none;
  }

  &__item {
    display: grid;
    grid-template-columns: 214px 120px 190px 190px 100px;
    gap: 30px;
    align-items: center;
    min-height: 150px;
    box-sizing: border-box;
    border-bottom: 1px solid $color-gainsboro;

    &:first-child {
      border-top: 1px solid $color-gainsboro;
    }
  }

  &__items {
    display: flex;
    align-items: center;
    padding-left: 15px;
    min-height: 40px;
    font-family: $font-family--main;
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.05em;
    color: $color-default-black;
    background-color: $color-default-white;
  }

  &__items-qty {
    font-weight: 700;
  }

  &__date {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 40px;
    font-family: $font-family--main;
    font-size: 20px;
    font-weight: 400;
    line-height: 25px;
    letter-spacing: 0.05em;
    color: $color-default-black;
    background-color: $color-default-white;
  }

  &__sum {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 40px;
    font-family: $font-family--main;
    font-size: 20px;
    font-weight: 700;
    line-height: 25px;
    letter-spacing: 0.05em;
    background-color: $color-default-white;
  }

  &__rouble {
    font-weight: 700;
    font-size: 20px;
    line-height: 15px;
    font-family: system-ui;

    margin-left: 5px;
  }

  &__number {
    margin: 0;
    font-family: $font-family--main;
    font-size: 20px;
    font-weight: 700;
    line-height: 25px;
    letter-spacing: 0.05em;
    color: $color-default-black;
    text-transform: uppercase;
  }

  &__remove-button {
    padding: 5px;
    min-height: 40px;
  }

  &__pagination {
    ul {
      margin-right: 7px;
    }
  }
}
