.form-review {
  display: flex;
  flex-direction: column;

  .rate__label {
    margin-left: 9px;
  }

  .rate__message {
    right: -10px;
    bottom: -21px;
    left: unset;

  }

  .rate--reverse {
    margin-left: 0;
  }
}

.form-review__wrapper {
  display: flex;
  margin-bottom: 5px;
  padding-top: 24px;
  justify-content: space-between;
}

.form-review__title {
  margin: 24px 0 32px;
  font-family: $font-family--main;
  font-weight: 600;
  font-size: 18px;
  line-height: 20px;
  color: $color-default-black;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}

.form-review__name-wrapper {
  width: 175px;
  margin-right: 30px;
}

.form-review__label {
  align-self: flex-start;
  display: inline-block;
  margin-bottom: 5px;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: 0.02em;
  color: $color-matterhorn-3;

  &--required {
    position: relative;

    &::before {
      content: "*";
      position: absolute;
      top: -5px;
      right: -10px;
      width: 5px;
      height: 5px;
      font-size: 12px;
      line-height: 16px;

      color: $color-free-speech-red;
    }
  }

  &--star {
    display: flex;
    justify-content: flex-end;
    margin-right: 12px;
    margin-bottom: 9px;
  }
}

.form-review__input {
  width: 100%;
  font-size: 14px;
  padding: 5px 8px;
  background-color: inherit;
  border: 1px solid $color-matterhorn-3;
  border-radius: 2px;
  outline: none;

  &--name {
    margin-bottom: 0;
  }

  &--textarea {
    height: 70px;
  }

  &::placeholder {
    font-size: 14px;
    line-height: 15px;
    letter-spacing: 0.02em;
    color: $color-default-black;
  }
}

.form-review__warning {
  display: block;
  text-align: end;
  margin: 3px 0 12px;
  font-size: 10px;
  line-height: 15px;
  letter-spacing: 0.02em;

  color: $color-flamingo;
}

.form-review__button {
  margin-top: 2px;
  margin-left: 70px;
  margin-right: 70px;
}

.form-review__input--textarea {
  height: 70px;
  outline: none;
}

.form-review__input--textarea + p {
  margin-bottom: 0;
}

.form-review__label--textarea {
  margin-bottom: 4px;
  margin-top: -5px;
}
