.catalog-cards {
  width: 100%;

  margin: 22px auto 0;

  &__list {
    padding: 0;
    margin: 0;
  }
}

.catalog-item {
  width: 100%;

  display: grid;
  grid-template-columns: 1fr 277px;
  column-gap: 20px;

  padding: 29px 0 17px;

  border-bottom: 1px solid $color-gainsboro;

  &:first-child {
    border-top: 1px solid $color-gainsboro;
  }
}

.catalog-item__data {
  display: grid;
  grid-template-columns: 36px 1fr;
  column-gap: 20px;
}

.catalog-item__data-wrapper {
  max-width: 340px;
}

.catalog-item__data img {
  width: 36px;
  height: 93px;

  object-fit: contain;
  display: block;
}

.catalog-item__data-title {
  font-weight: 700;
  font-size: 16px;
  line-height: 15px;
  letter-spacing: 0.05em;
  text-transform: uppercase;

  margin: 0 0 10px;
}

.catalog-item__data-rate {
  fill: $color-default-black;

  margin: 0 0 10px;

  & svg {
    margin-right: 1px;
  }
}

.catalog-item__data-date {
  font-size: 14px;
  line-height: 15px;
  letter-spacing: 0.05em;

  margin: 0 0 10px;
}

.catalog-item__data-price {
  font-weight: 700;
  font-size: 20px;
  line-height: 15px;
  letter-spacing: 0.05em;
  font-family: system-ui;

  margin: 0;
}

.catalog-item__buttons {
  display: grid;
  grid-template-columns: 120px 120px;
  column-gap: 37px;
  align-items: center;

  & a,
  & button {
    width: 120px;
    min-height: 40px;

    display: flex;
    align-items: center;
    justify-content: center;

    font-size: 11px;
    line-height: 15px;

    padding: 5px;
    margin: 5px 0 5px 0;
  }
}
