.modal-enter {
  display: flex;
  flex-direction: column;
  align-items: center;

  &__title {
    margin: 74px 0 29px;
    width: 255px;
    font-family: $font-family--main;
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
    letter-spacing: 0.02em;
    color: $color-default-black;
    text-align: center;
  }

  &__link {
    margin-bottom: 23px;
    min-width: 170px;
    font-family: $font-family--main;
    font-weight: 700;
    font-size: 12px;
    line-height: 15px;
    letter-spacing: 0.05em;
    padding: 16px;
  }

  &__text {
    margin: 0;
    font-family: $font-family--main;
    font-weight: 600;
    font-size: 12px;
    line-height: 20px;
    letter-spacing: 0.05em;
    color: $color-default-black;
    text-align: center;

    a {
      font-weight: 700;
      font-size: 14px;
      line-height: 20px;
      color: $color-free-speech-red;
      text-decoration: underline;
      text-underline-offset: 4px;
    }
  }
}
