.order-list {
  padding: 0;
  margin: 0 0 30px;

  &__item {
    display: grid;
    align-items: center;
    grid-template-columns: 560px min-content 1fr;
    padding: 21px 10px 25px 44px;
    position: relative;

    border-bottom: 1px solid $color-gainsboro;

    &:first-of-type {
      border-top: 1px solid $color-gainsboro;
    }
  }

  &__data {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;

    max-width: 540px;
  }

  & img {
    margin: 0 45px 0 0;
  }

  &__container {
    margin: 0 auto 0 0;
  }

  &__name {
    font-weight: 700;
    font-size: 16px;
    line-height: 18px;
    letter-spacing: 0.05em;
    text-transform: uppercase;

    margin: 28px 0 44px;
  }

  &__lot,
  &__parameters {
    font-size: 14px;
    line-height: 15px;
    letter-spacing: 0.05em;

    margin: 0 0 6px;
  }

  &__parameters {
    margin: 0;
  }

  &__quantity {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 8px;
    min-height: 40px;
    min-width: 40px;

    font-size: 18px;
    line-height: 15px;
    letter-spacing: 0.05em;

    background-color: $color-default-white;
  }

  &__price {
    margin-left: auto;
    font-weight: 700;
    font-size: 20px;
    line-height: 22px;
    letter-spacing: 0.05em;
    font-family: system-ui;
  }

  &__button {
    position: absolute;
    top: 32px;
    right: 0;
  }
}
