.footer {
  padding-top: 66px;
  padding-bottom: 15px;
  min-height: 275px;
  background-color: $color-seal-brown;

  &__container {
    display: grid;
    grid-template-columns: 156px 221px 151px 1fr;
    grid-column-gap: 64px;
  }

  &__logo-wrapper {
    display: flex;
    flex-direction: column;
  }

  &__logo {
    margin-right: auto;
    margin-bottom: 61px;
  }

  &__nav-section {
    display: flex;
    flex-direction: column;
  }

  &__nav-title {
    margin: 0 0 12px;
    font-family: $font-family--secondary;
    font-size: 14px;
    font-weight: 400;
    line-height: 44px;
    letter-spacing: 0.13em;
    color: $color-snow;

    &--links,
    &--contacts {
      opacity: 0.7;
    }
  }

  &__nav-text {
    margin: 0;
    font-family: $font-family--secondary;
    font-size: 10px;
    font-weight: 400;
    letter-spacing: 0.03em;
    color: $color-snow;

    &--about {
      margin: 0 0 17px;
      line-height: 17px;
    }

    &--address {
      font-family: $font-family--main;
      line-height: 20px;
      letter-spacing: 0.1em;
    }

    &--work-hours-title {
      margin-bottom: 10px;
      font-family: $font-family--main;
      line-height: 12px;
      letter-spacing: 0.1em;
    }

    &--work-hours {
      margin-left: 14px;
      font-family: $font-family--main;
      line-height: 12px;
      letter-spacing: 0.1em;
    }

    &--weekends {
      font-family: $font-family--main;
      line-height: 12px;
      letter-spacing: 0.1em;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__nav-list {
    padding: 0;
    margin: 0;
    font-size: 10px;
    line-height: 20px;
    list-style: none;
  }

  &__nav-link {
    margin: 5px 2px 4px;
    font-family: $font-family--main;
    font-size: 10px;
    font-weight: 400;
    line-height: 21px;
    letter-spacing: 0.1em;
    color: $color-snow;

    &--phone {
      margin-top: 7px;
      margin-bottom: 8px;
      line-height: 13px;
    }
  }
}
