.product-list {
  margin: 0 auto -60px;

  &__title {
    font-weight: 700;
    font-size: 22px;
    line-height: 30px;

    margin: 0 0 15px;
  }

  & .catalog {
    margin: 0 0 47px;
  }

  & .catalog-sort {
    padding: 2px 0 3px;
  }

  & .catalog-sort__title {
    margin: 0 34px 0 0;
  }

  & .catalog-sort__type-button {
    margin: 0 39px 5px 0;
  }

  & .catalog-filter__title {
    margin: 71px 0 25px;
  }

  & .catalog-filter__block-title {
    margin: 0 0 29px;
  }

  & .catalog-filter__block-item {
    margin: 0 0 20px;
  }

  & .catalog-filter__block {
    padding: 25px 0 22px;

    &:last-of-type {
      padding: 25px 0 0;
    }
  }

  & .catalog-filter__reset-btn {
    width: 100px;
    min-width: unset;
    min-height: 40px;

    font-size: 11px;
    line-height: 15px;

    padding: 5px;
    margin-left: 10px;
  }

  &__button {
    width: 200px;

    font-weight: 700;
    font-size: 11px;
    line-height: 15px;
    letter-spacing: 0.02em;
    color: $color-snow;

    padding: 10px;
    margin: 0 auto 38px;
  }

  & .pagination__list {
    margin: 0 0 -20px;

    z-index: 1000;
  }

  & .pagination__page-link {
    z-index: 10;
  }

}
