.input-login {
  display: flex;
  flex-direction: column;

  padding: 0 5px;
  margin-bottom: 49px;

  &:last-of-type {
    margin-bottom: 47px;
  }

  & label {
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
    letter-spacing: 0.02em;

    width: max-content;
    margin-bottom: 13px;

    position: relative;

    &::after {
      content: "*";
      position: absolute;
      top: -2px;
      right: -7px;
      width: 5px;
      height: 5px;
      font-size: 12px;
      line-height: 16px;

      color: $color-free-speech-red;
    }
  }

  & input {
    width: 100%;

    border: 1px solid $color-matterhorn-3;
    border-radius: 2px;
    background-color: transparent;
    outline: none;

    padding: 16px;
    margin: 0;

    &::placeholder {
      color: $color-matterhorn-4;
    }
  }

  & span {
    position: relative;
  }

  &__button-eye {
    position: absolute;
    cursor: pointer;

    top: 7px;
    right: 17px;

    border: none;
    background-color: transparent;
    fill: transparent;

    padding: 8px;

    &:hover,
    &:focus,
    &:active {
      opacity: 0.7;
      transition: opacity $trans--300;
    }
  }

  &__error {
    font-size: 10px;
    line-height: 15px;
    font-weight: 400;
    color: $color-flamingo;

    margin: 11px 5px 0 auto;
  }

  &--no-error .input-login__error {
    display: none;
  }
}
