.input-radio {
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  span {
    margin-bottom: 42px;
    font-family: $font-family--main;
    font-size: 20px;
    line-height: 15px;
    letter-spacing: 0.05em;
    color: $color-default-black;
  }

  input {
    position: absolute;
    margin: -1px;
    width: 1px;
    height: 1px;
    clip: rect(0 0 0 0);
  }

  label {
    position: relative;
    display: inline-block;
    padding-left: 49px;
    margin-bottom: 17px;
    font-family: $font-family--main;
    font-size: 16px;
    font-weight: 400;
    line-height: 22px;
    color: $color-default-black;
    cursor: pointer;

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      width: 28px;
      height: 28px;
      transform: translateY(-50%);
      border: 2px solid #d9d9d9;
      border-radius: 50%;
      background-color: $color-default-white;
    }
  }

  input:checked + label {
    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      width: 28px;
      height: 28px;
      transform: translateY(-50%);
      border: 2px solid $color-free-speech-red;
      border-radius: 50%;
      background-color: transparent;
    }

    &::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 8px;
      width: 12px;
      height: 12px;
      transform: translateY(-50%);
      border-radius: 50%;
      background-color: $color-free-speech-red;
    }
  }
}
