.order {
  margin: 0 0 -29px;

  & .container {
    min-height: 1215px;

    display: flex;
    flex-direction: column;
  }

  &__title {
    font-weight: 700;
    font-size: 22px;
    line-height: 30px;

    margin: 0 0 15px 0;
  }

  & .breadcrumbs {
    margin: 0 0 33px 0;
  }

  &__button {
    position: relative;
    font-weight: 700;
    font-size: 11px;
    line-height: 15px;
    letter-spacing: 0.02em;

    width: 100%;
    max-width: 200px;
    min-height: 40px;

    margin: auto auto 0;
    z-index: 1;
  }
}
