.error {
  display: flex;
  flex-direction: column;
  align-items: center;

  margin: 178px auto 66px;

  &__title {
    font-size: 128px;
    line-height: 132px;
    letter-spacing: 0.1em;

    margin: 0 auto 10px;
  }

  &__subtitle {
    font-weight: 700;
    font-size: 36px;
    line-height: 40px;
    letter-spacing: 0.02em;

    margin: 0 auto 50px;
  }

  &__text {
    font-size: 18px;
    line-height: 30px;
    letter-spacing: 0.02em;
    text-align: center;

    margin: 0 auto 70px;
  }
}
