.login {
  width: 551px;

  background-color: $color-default-white;
  box-shadow: 0px 5px 6px $color-eclipse-20;
  border-radius: 4px;

  padding: 76px 106px 78px;
  margin: 35px auto -41px;

  &__title {
    font-size: 24px;
    line-height: 28px;
    letter-spacing: 0.02em;

    margin: 0 0 36px;
  }

  &__text {
    font-size: 10px;
    line-height: 16px;
    letter-spacing: 0.05em;
    text-transform: uppercase;

    margin: 0 0 65px;
  }

  &__link {
    color: $color-free-speech-red;
    text-decoration: underline;
  }

  &__button {
    width: 100%;
    max-width: 210px;
    min-height: 50px;

    font-size: 14px;
    line-height: 15px;
    letter-spacing: 0.05em;

    padding: 10px;
    margin: 0 auto;
  }
}
