.custom-textarea {
  label {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  span {
    margin-right: auto;
    margin-bottom: 41px;
    font-family: $font-family--main;
    font-size: 20px;
    font-weight: 400;
    line-height: 25px;
    letter-spacing: 0.05em;
    color: $color-default-black;
  }

  textarea {
    padding: 55px 25px;
    width: 100%;
    height: 400px;
    font-family: $font-family--main;
    font-size: 20px;
    font-weight: 400;
    line-height: 35px;
    letter-spacing: 0.05em;
    color: $color-default-black;
    box-sizing: border-box;
    border: 1px solid $color-default-black;
    border-radius: 2px;
    outline: none;
    background-color: $color-default-white;

    &::placeholder {
      line-height: 22px;
      color: $color-grey;
    }
  }

  sub {
    top: -10px;
    color: $color-free-speech-red;
  }

  p {
    position: relative;
    margin: 0;
    width: 100%;
    font-family: $font-family--main;
    font-size: 10px;
    font-weight: 400;
    line-height: 15px;
    letter-spacing: 0.02em;
    color: $color-flamingo;
    transition: opacity $trans--300;
    text-align: right;
    opacity: 0;
  }

  &.is-invalid {
    p {
      opacity: 1;
    }
  }
}
